.subscribe__book {
  @include full-bleed;
  background-color: $color-navy;
  color: $white;
}

.subscribe__book-container {
  margin: 0 auto;
  max-width: $site-max-width;
  padding: rem(120) 2rem;
  text-align: center;

  @include breakpoints(none, medium) {
    padding: rem(40) 1rem;
  }

  input {
    color: $steel;
    font-size: rem(18);
    font-family: font-family(sans-serif);
    padding: rem(15);
    width: rem(370);
    height: rem(56);
  }

  button {
    appearance: none;
    border: none;
    background-color: $color-darktan;

    &:hover,
    &:focus {
      background-color: $color-darkdarktan;
      cursor: pointer;
    }
  }
}

.subscribe__book-header {
  font-size: rem(36);
  font-weight: 600;
  line-height: 1;
  margin-bottom: rem(20);
  text-align: center;

  @include breakpoints(none, medium) {
    font-size: rem(26);
    text-align: left;
    line-height: 1.2;
  }
}

.subscribe__book-text {
  font-size: rem(24);
  font-weight: 600;
  margin-bottom: rem(30);

  @include breakpoints(none, medium) {
    font-size: rem(20);
    text-align: left;
    line-height: 1.2;
  }
}

.subscribe__book-signup {
  align-items: center;
  display: flex;
  justify-content: center;
}

.subscribe__book-submit {
  height: rem(56);
  width: rem(56);
  position: relative;

  svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
  }
}

/* stylelint-disable selector-max-specificity */

.subscribe__book-form {
  display: flex;
  max-width: 375px;
  margin: 0 auto;
  height: 44px;
  // stylelint-disable-next-line declaration-no-important
  padding: 0 !important;

  .mktoFormRow {
    flex: 1;
    width: 100%;
    // stylelint-disable-next-line declaration-no-important
    display: none !important;

    &:first-of-type {
      // stylelint-disable-next-line declaration-no-important
      display: inline-block !important;
    }

    .mktoFieldDescriptor {
      // stylelint-disable-next-line declaration-no-important
      margin: 0 !important;
      height: 100%;
    }

    .mktoFieldWrap {
      height: 100%;

      .mktoEmailField {
        flex: 1;
        width: 100%;
        background: $white;
        padding: 6px 15px;
        border: 1px solid $white;
        outline: none;
        font-size: 16px;
        font-family: font-family(sans-serif);
        // stylelint-disable-next-line declaration-no-important
        height: 100% !important;
      }
    }
  }

  &.mktoForm {
    .mktoGutter,
    label {
      // stylelint-disable-next-line declaration-no-important
      display: none !important;
    }
  }

  .mktoButtonRow {
    width: 95px;
    height: 44px;

    .mktoButtonWrap {
      button.mktoButton {
        width: 95px;
        height: 44px;
        // stylelint-disable-next-line declaration-no-important
        // stylelint-disable-next-line declaration-no-important
        outline: none;
        padding: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        color: $white;
        // stylelint-disable-next-line declaration-no-important
        font: 0.8rem / 1 font-family(sans-serif) !important;

        &::after {
          content: 'Sign Up';
          // stylelint-disable-next-line declaration-no-important
          font: 1rem / 1 font-family(sans-serif) !important;
        }

        &[disabled] {
          &::after {
            content: none;
          }
        }

        > img {
          display: none;
        }

        .subscribe-input__btn {
          display: none;
          height: 20px;
          max-width: 16px;
          width: 16px;
        }
      }
    }
  }

  input:not([type="submit"]) {
    flex: 1;
    width: 100%;
    background: $white;
    padding: 6px 15px;
    border: 1px solid $white;
    outline: none;
    font-size: 16px;
  }

  button {
    width: 44px;
    height: 44px;
    border: none;
    outline: none;
    padding: 0;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $white;
  }
}

.thanks-msg {
  display: flex;
  flex-direction: column;
  justify-content: center;

  .title-wrap {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;

    img {
      width: 45px;
      margin: -18px 15px 0 0;
    }
  }

  h2 {
    font-size: rem(36);
    margin-bottom: rem(24);
    font-weight: 600;
    line-height: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    position: relative;
    align-items: flex-start;
    padding-left: 65px;
    padding-top: 20px;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 45px;
      height: 45px;
      background: url("data:image/svg+xml,%3Csvg width='46' height='46' viewBox='0 0 46 46' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M35.6254 24.3987C36.4397 28.2706 35.9178 32.3041 34.145 35.8412C32.3722 39.3784 29.453 42.2104 25.8638 43.8753C22.2746 45.5401 18.2272 45.9396 14.3817 45.0085C10.5363 44.0774 7.11986 41.8707 4.68977 38.7484C2.25969 35.626 0.959377 31.7724 1.00097 27.816C1.04256 23.8597 2.42359 20.0342 4.91878 16.9637C7.41396 13.8931 10.876 11.7587 14.7402 10.9087C18.6043 10.0586 22.6425 10.5431 26.1959 12.283' stroke='%23ED8B00'/%3E%3Cpath d='M8.5 26L17.5 35L45.5 1' stroke='white'/%3E%3C/svg%3E%0A") no-repeat 50% 50% / contain;
    }
  }

  p {
    padding-left: 40px;
  }
}

/* stylelint-enable selector-max-specificity */
