.book__author {
  @include full-bleed;
  background-color: $white;
}

.book__author-wrapper {
  color: $steel;
  justify-content: space-between;
  display: flex;

  @include breakpoints(none, large) {
    flex-direction: column;
  }

  p {
    line-height: 1.63;
    margin-bottom: rem(20);
  }

  img {
    @include breakpoints(none, large) {
      margin-bottom: rem(20);
    }
  }
}

.book__author-img {
  flex-basis: 45%;
}

.book__author-about {
  flex-basis: 52%;
}
