.book__purchase {
  background-color: $color-beige;
  text-align: center;
  margin-left: 50%;
  transform: translateX(-50%);
  width: 100vw;
}

.book__purchase-content {

  a {
    color: $color-darktan;
    text-decoration: none;

    &:hover,
    &:focus {
      color: $color-darkdarktan;
      cursor: pointer;
    }
  }

  p {
    margin-bottom: rem(30);
  }
}

.book__purchase-logos {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  width: 100%;
  margin: 0 -8px;

  @supports (display: grid) {
    display: grid;
    grid-gap: rem(15);
    grid-template-columns: repeat(2, 1fr);
    margin: 0;

    @include breakpoints(large) {
      grid-template-columns: repeat(4, 1fr);
    }
  }
}

.book__purchase-entry {
  background-color: $white;
  display: flex;
  height: 197px;

  @media (-ms-high-contrast: active), (-ms-high-contrast: none) {
    width: calc(25% - 16px);
    margin: 0 8px 16px;
  }

  a {
    border: 1px solid $color-darktan;
    display: flex;
    height: 100%;
    width: 100%;

    &:hover,
    &:focus {
      border-color: $color-navy;

      // stylelint-disable-next-line selector-max-specificity
      img {
        filter: hue-rotate(180deg);
      }

      // stylelint-disable-next-line selector-max-specificity
      .alt {
        display: block;
        filter: none;
      }

      // stylelint-disable-next-line selector-max-specificity
      .original {
        display: none;
      }
    }
  }

  img {
    display: block;
    margin: auto;

    &.alt {
      display: none;
    }
  }
}
