/*
 * Post List module styles
 */

// Common Post List Styles
.post-list {
	@include vertical-rhythym(spacer(1));
}

.post-list__item {
	position: relative;
}

.post-list__item-title {
	font-size: rem(22);
}

.post-list__item-title-link {
	@include link-hover();
}

.byline--post-list {

	a {
		font-weight: 500;
	}
}

.post-list__item-read-more {
	@include link-hover();
	text-transform: lowercase;

	.home & {
		font-weight: 500;
	}
}

.post-list__item-tags {
	color: $color-secondary;
	display: flex;
	flex-wrap: wrap;
	font-size: rem(12);
	text-transform: lowercase;

	li {
		@include separator('-', rem(6));
	}

	a {
		@include link-hover($color-secondary);
	}
}

.post-list__item-source {
	@include link-hover($color-secondary);
	font-size: rem(12);
}

// Overrides for "Post" type Post List
.post-list--posts {
	@include vertical-rhythym(spacer(2), rem(20), $border-light);

	// Load More Posts Styles
	&.wrap-ajax-loaded {
		border-top: $border-light;
		margin-top: spacer(2);
		padding-top: rem(20);
		width: 100%;
	}

	.byline--post-list {
		margin-bottom: spacer(1);
	}

	/* stylelint-disable selector-max-specificity */
	/* Target only read more links of post lists in home page popular section */
	.post-list__item-read-more {

		.home-section--popular & {
			display: block;
			margin-bottom: spacer(1);
		}
	}
	/* stylelint-enable */

	.post-list__item-title {
		@include post-header();
	}
}

// Overrides for Featured type Post List
.post-list--featured,
.post-list--posts {

	.post-list__item {
		@include clearfix;
	}

	.post-list__item-thumbnail {
		display: block;
		margin-bottom: 1em;

		@include breakpoints(small) {
			float: right;
			margin-left: rem(24);
			max-width: rem(360);
		}

		img {
			display: block;
			width: 100%;
		}
	}
}

// Overrides for Featured type Post List
.post-list--events {

	.post-list__item-header {
		align-items: flex-end;
		display: flex;
	}

	.post-list__item-title {
		font-weight: 500;
		margin-right: spacer(1);
	}

	.post-list__item-date {
		color: color(gray-very-light);
		font-size: rem(12);
	}
}

// Overrides for Releated Stories type Post List
.post-list--related {
	@include vertical-rhythym(spacer(3.3));

	.post-list__item-title {
		line-height: 1.25;
		font-size: rem(20);

		@include breakpoints(medium) {
			font-size: rem(22);
		}
	}
}

// Overrides for Tweet module post list
.post-list--tweets {
	@include vertical-rhythym(spacer(1.5));
}
