/* stylelint-disable selector-max-specificity */
.a16z_book-template-default {
  background-color: $color-beige;
  padding: 0;

  .site-header {
    background-color: $color-beige;
    margin-bottom: 0;

    @include breakpoints(large) {
      padding-left: 2rem;
      padding-right: 2rem;
    }

    svg {
      @include breakpoints(none, medium) {
        background-color: $color-beige;
      }
    }
  }

  .site-content__primary {
    margin: 0;
  }

  .site-header__mobile-toggle {
    background-color: $color-beige;
  }

  .site-content {
    margin-bottom: 0;

    @include breakpoints(none, large) {
      padding: 0;
    }
  }

  &.full-bleed-banner .introduction__entry-header {
    padding-top: 110px;
  }

  .introduction__entry-header {
    background-color: $color-beige;
    margin-left: 50%;
    width: 100vw;
    transform: translateX(-50%);
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;

    .content-holder {
      max-width: 1132px;
      margin: 0 auto;
      display: flex;
      align-items: flex-start;

      @include breakpoints(none, medium) {
        padding: 1rem 1rem 3rem 1rem;
      }

      @include breakpoints(medium, large) {
        padding: 1rem 2rem 0 2rem;
      }

      @include breakpoints(large) {
        padding: rem(90) 2rem 0 2rem;
      }

      &.one-column {
        align-items: center;
        justify-content: center;
        text-align: center;

        * {
          max-width: none;
        }
      }
    }

    .introduction {
      color: $color-darktan;
      font-size: rem(18);
      font-weight: 400;
      line-height: 1;
      margin-bottom: rem(15);

      @include breakpoints(large) {
        font-size: rem(24);
        margin-bottom: rem(5);
      }
    }

    .entry-header__title {
      font-size: rem(30);
      font-weight: 600;
      line-height: 1.2;
      margin-bottom: 1rem;
      max-width: rem(450);

      @include breakpoints(large) {
        font-size: rem(50);
      }
    }

    .introduction__subtitle {
      font-size: 1rem;
      max-width: rem(450);
    }
  }

  .attachment-book-cover-intro {
    display: block;
  }

  .introduction__right {
    margin-left: auto;
    align-self: flex-end;

    @include breakpoints(none, medium) {
      display: none;
    }
  }

  .introduction_left {
    padding-top: rem(30);

    @include breakpoints(medium, none) {
        padding-bottom: rem(35);
    }

    @include breakpoints(none, medium) {
      width: 100%;
    }
  }

  .btn-simple {
    background-color: $color-darktan;
    color: $white;
    display: inline-flex;
    align-items: center;
    font-size: rem(18);
    font-weight: 900;
    margin-bottom: rem(20);
    margin-top: rem(20);
    padding: 10px 15px;
    text-decoration: none;
    text-transform: uppercase;

    @include breakpoints(large) {
      margin-top: rem(30);
    }

    &:hover,
    &:focus {
      background-color: $color-darkdarktan;
    }

    &::after {
      content: '';
      margin-left: rem(10);
    }

  }
}

.post-password-form-wrapper {
    display: flex;
    padding: rem(40 0 40);
    min-height: calc(100vh - rem(96));
    align-items: center;
    justify-content: center;
}

.post-password-form {
    flex: 1 0 auto;
    width: 100%;
    border-radius: rem(24 0 24 24);
    position: relative;
    z-index: 1;
    background: $white;
    max-width: 332px;
    padding: rem(24 24 40 24);
    margin: 0 auto;

    label {
        font-weight: 600;
        font-size: rem(16);
        line-height: 1.5;
        letter-spacing: -0.005em;
        color: $steel;
        display: block;
    }

    [name="post_password"] {
        display: block;
        background: #FBFBFB;
        border: 1px solid #E5E6E5;
        line-height: rem(36);
        margin: rem(5 0 16);
        width: 100%;
    }

    [type="submit"] {
        width: 100%;
        display: inline-block;
        vertical-align: top;
        background: $poppy;
        color: #fff;
        padding: 11px 18px;
        font-size: 16px;
        font-weight: 500;
        border: none;
        outline: none;
        transition: all 0.3s ease-in-out;

        &:hover {
            background: #ff9f17;
        }
    }

    h4 {
        margin: rem(60 0 10);
        font-weight: 700;
        font-size: 34px;
        line-height: 1.12;
        letter-spacing: -0.015em;
        color: $bayside;
    }
}

.password-protected {
    position: relative;

    .decor-left {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;

        img {
            display: block;
            height: 100%;
        }
    }

    .decor-right {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;

        img {
            display: block;
            height: 100%;
        }
    }

    .footer-new {
        position: relative;
        z-index: 1;
    }

    &.navbar-color-light {
        color: $white;

        .copyright {
            color: $white;
        }

        .footer-nav {
            ul {
                li {
                    a {
                        color: $white;
                    }

                    + li::before {
                        background: $white;
                    }
                }
            }
        }

        .social-networks li a {
            background: $white;
        }
    }
}

/* stylelint-enable */
