.detail__book {
  @include full-bleed;
  background-color: $white;
}

.detail__book-container {
  margin: 0 auto;
  max-width: $site-max-width;
  padding: rem(40) 1rem;

  @include breakpoints(medium) {
    padding: rem(70) 2rem;
  }
}

.detail__container {
  align-items: flex-start;
  display: flex;

  @include breakpoints(none, medium) {
    flex-direction: column;
  }
}

.detail__book-cover {
  flex-basis: 40%;
  margin-bottom: rem(20);

  @include breakpoints(medium) {
    margin-bottom: 0;
    margin-right: rem(30);
  }

  @include breakpoints(large) {
    margin-left: rem(50);
    margin-right: 0;
  }
}

.detail__book-text {
  flex-basis: 60%;

  .entry-header__title {
    font-size: rem(20);
    font-weight: 600;
    margin-bottom: rem(20);

    @include breakpoints(large) {
      font-size: rem(32);
      margin-bottom: rem(30);
    }
  }
}

.detail__book-content {
  @import '../site/wp-content';
}
