.book__reviews {
  background-color: $color-beige;
  margin-left: 50%;
  transform: translateX(-50%);
  width: 100vw;

  h2 {
    text-align: center;
  }
}

.book__reviews-grid {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  width: 100%;

  @supports (display: grid) {
    display: grid;
    grid-gap: rem(15);
    grid-template-columns: repeat(1, 1fr);

    @include breakpoints(medium) {
      grid-template-columns: repeat(2, 1fr);
    }

    @include breakpoints(large) {
      grid-template-columns: repeat(4, 1fr);
    }
  }
}

.book__reviews-entry {
  background-color: $white;
  border: 1px solid $color-darktan;
  display: flex;
  flex-direction: column;
  height: rem(280);
  padding: rem(20);

  @include breakpoints(large) {
    height: rem(407);
    padding: rem(35) rem(25);
  }
}

.book__reviews-content {
  font-size: 1rem;
  line-height: 1.2;

  @include breakpoints(large) {
    font-size: rem(22);
  }
}

.book__reviews-logo {
  margin-bottom: rem(30);
  display: block;

  img {
    @include breakpoints(none, large) {
      max-height: rem(100);
      width: auto;
    }
  }
}

.readmore-link {
  color: $color-darktan;
  display: block;
  font-size: 1rem;
  margin-top: auto;
  text-decoration: none;

  &::after {
    content: '';
    display: inline-block;
    margin-left: rem(10);
    transition: 0.25s transform ease-in;
  }

  &:hover,
  &:focus {
    color: $color-darkdarktan;
    cursor: pointer;

    &::after {
      transform: translateX(rem(10));
    }
  }
}
