// Content Single post content styles
/* stylelint-disable selector-max-specificity */
.entry-content {
	margin-bottom: rem(60);

  &.has-prev-next {
    @include breakpoints($to:s-large) {
      margin-bottom: rem(30);

      .widget--subscribe {
        margin-bottom: rem(30);
      }
    }

    + .entry-footer {
      .entry-footer__related-posts {
        border-top: 0 none;
        padding-top: 0;
      }
    }
  }
}
/* stylelint-enable selector-max-specificity */
.post-terms--single {

	li {
		@include separator('-', rem(3));

		&::after {
			content: '\2022';
		}
	}
}

.wp-content {
	// For overrides to global wp-content, see ../site/site.scss
	// For overrides to single post content, use .entry-content__post-body
}

.entry-content__post-body {
	margin-bottom: rem(24);
}

.entry-content__post-date {
	color: color(gray-very-light);
	font-size: rem(12);
	text-align: right;

	.blog-content-area & {
		padding-top: rem(20);
	}

	@include breakpoints(medium) {
		padding-bottom: rem(40);

		.blog-content-area & {
			padding-top: rem(40);
		}
	}
}

.entry-content__related-posts {
	@include breakpoints(medium) {
		padding-top: rem(40);
		border-top: $border-light;

	}
}

.entry_content__disclaimer {
	font-family: font-family(sans-serif);
	font-style: italic;
	font-size: rem(16);

	a {
		color: $poppy;
		text-decoration: none;
	}
}

.wp-video {
	margin-left: auto;
	margin-right: auto;
}

