// stylelint-enable selector-max-specificity

.entry-content-prev,
.entry-content-next {
  text-decoration: none;
  display: block;
  position: absolute;
  z-index: 1;

  @include breakpoints(s-large) {
    position: fixed;
    top: 50%;
    margin-top: -20px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 300ms ease-out, visibility 300ms ease-out;

    .show-prev-next & {
      opacity: 1;
      visibility: visible;
    }
  }

  i {
    position: absolute;
    top: 50%;
    color: $poppy;
    text-decoration: none;
    font-size: 16px;
    line-height: 1;
    z-index: 2;
    border-radius: 50%;
    transition: color 0.3s ease-in, background-color 0.5s ease-in;
    transform: translateY(-50%);

    @include breakpoints(s-large) {
      color: $concrete;
    }

    &::after {
      content: "";
      border: 2px $poppy solid;
      width: 40px;
      height: 40px;
      overflow: hidden;
      position: absolute;
      border-radius: 999px;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      z-index: -1;

      @include breakpoints(s-large) {
        border-color: $concrete;
      }
    }
  }

  &:hover {
    > i {
      color: $white;

      // stylelint-disable-next-line selector-max-specificity
      &::after {
        background-color: $poppy;
        border-color: $poppy;
      }
    }
  }

  .icon-chevron-left {
    left: 30px;

    @include breakpoints($to:s-1350) {
      left: 25px;
    }
  }

  .icon-chevron-right {
    right: 30px;

    @include breakpoints($to:s-1350) {
      right: 25px;
    }
  }

  .box {
    color: $steel;
    z-index: 1;

    background: $white;
    padding: 19px 18px;
    font: 600 16px/1.6 font-family(sans-serif);
    border: 1px solid #ebebeb;

    min-width: 250px;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: transform 0.3s ease-out;

    @include breakpoints(s-large) {
      box-shadow: 6px 6px 0 #ebebeb;
    }

    @include breakpoints(s-large) {
      opacity: 0;
      transition: transform 0.3s ease-out, opacity 0.3s ease-out;
    }

    @include breakpoints($to:s-medium) {
      //display: none;
      display: flex;
      align-items: center;
    }
  }
}

.entry-content-prev {
  left: 0;

  // stylelint-disable-next-line selector-max-specificity
  &:hover .box {
    @include breakpoints(s-large) {
      transform: translateY(-50%);
      opacity: 1;
    }
  }

  .box {
    padding-left: 100px;
    left: 0;
    transform: translate3d(calc(-100% - 10px), -50%, 0);

    @include breakpoints($to:s-large) {
      padding-left: 62px;
      transform: translateY(-50%);
      width: 50vw;
      max-width: 350px;
      min-width: 0;
    }
  }
}

.widget--subscribe-footer {
  ~ .entry-content__related-posts {
    @include breakpoints(medium) {
      padding-top: 0;
      border-top: 0 none;
    }
  }
}

.entry-content-prev-next--desktop {
  display: none;

  @include breakpoints(s-large) {
    display: block;
  }

  body:not(.ab-test) & {
    // stylelint-disable-next-line declaration-no-important
    display: none !important;
  }
}

/* stylelint-disable selector-max-specificity */
.entry-content-next {
  right: 0;

  &:hover .box {
    @include breakpoints(s-large) {
      transform: translate3d(0, -50%, 0);
      opacity: 1;
    }
  }

  .box {
    padding-right: 100px;

    right: 0;
    transform: translate3d(calc(100% + 10px), -50%, 0);

    @include breakpoints(s-large) {
      box-shadow: -6px 6px 0 #ebebeb;
    }

    @include breakpoints($to:s-large) {
      padding-right: 62px;
      transform: translateY(-50%);
      width: 50vw;
      width: calc(50vw + 1px);
      min-width: 0;
      max-width: 350px;
    }
  }
}

.entry-content-prev-next--mobile {
  //position: relative;
  //top: -1px;
  display: flex;
  margin: 0 -20px;
  overflow: hidden;
  border-top: 1px solid #D1D1D1;
  border-bottom: 1px solid #D1D1D1;

  body:not(.ab-test) & {
    // stylelint-disable-next-line declaration-no-important
    display: none !important;
  }

  .widget--subscribe ~ & {
    margin-top: -30px;
  }

  @include breakpoints(s-large) {
    display: none;
  }

  a {
    position: relative;
    display: flex;
    flex-direction: column;
    width: 50%;
    flex: 0 0 50%;
  }

  .entry-content-next {
    border-left: 1px solid #D1D1D1;

    .box {
      text-align: right;
    }
  }

  .entry-content-next,
  .entry-content-prev {
    .box {
      padding: 0 18px 18px 18px;
      position: relative;
      //height: 100%;
      // stylelint-disable-next-line declaration-no-important
      transform: none !important;
      top: 0;
      border: none;
      width: 100%;
    }

    .arrows {
      position: relative;
      color: $poppy;
      font-weight: 600;
      font-size: 16px;
      padding: 20px;
    }

    &:hover {
      .arrows > i {
        color: $white;

        &::after {
          background-color: $poppy;
          border-color: $poppy;
        }
      }
    }
  }

  .entry-content-next .arrows {
    padding-right: 65px;
    text-align: right;
  }

  .entry-content-prev .arrows {
    padding-left: 65px;
  }
}
