.book-quotes {
  @include full-bleed;
  background-color: $color-navy;

  .book-quotes-slider-controls {
      margin-left: 0;
      display: flex;
      justify-content: center;
  }
}

.book-quotes__wrapper {
  align-items: center;
  color: $white;
  font-size: rem(14);
  line-height: rem(20);
  margin-bottom: rem(10);
  text-align: center;

   @include breakpoints(large) {
     font-size: rem(17);
     line-height: rem(26);
   }

  .book__section-header {
    color: $white;
  }
}

.book-quotes__content {
 color: $white;
 display: block;

 p {
   margin-bottom: rem(15);
 }
}

.book-quotes__title {
  display: block;
  font-weight: 600;
  line-height: 1;
}

.book-quotes__entry {
  margin: 0 auto;
  position: relative;
  width: 100%;

  svg {
    width: 80px;
    color: #a9903e;

    @include breakpoints(large) {
      position: absolute;
      top: calc(50% - 80px);
    }

    @include breakpoints(none, large) {
      margin: 0 auto rem(10) auto;
    }
  }

  svg:not(.last) {
    left: 0;
  }

  svg.last {
    right: 0;

    @include breakpoints(none, large) {
      display: none;
    }
  }

  &:not(:last-of-type) {
    margin-bottom: rem(50);
  }
}

.book-quotes__entry-container {
  margin: 0 auto;
  width: 85%;
}

.book-quotes__slider {
  padding: rem(40) 0 rem(30); // Fix overflow cut off from Siema.
}

.fill-current {
  fill: currentColor;
}
