.book__promo {
  @include full-bleed;
  background-color: $poppy; // todo: featured img as bg img
}

.book__promo-wrapper {
  color: $white;
  position: relative;
  text-align: center;

  .book__section-header {
    color: $white;
  }

  p {
    font-size: rem(14);
    line-height: rem(20);

    @include breakpoints(large) {
      font-size: rem(20);
      line-height: rem(26);
    }
  }

  img {
    height: 100%;
    min-width: 100vw;
    object-fit: cover;
    position: absolute;
    top: 0;
    transform: translateX(-50%);
    width: 100vw;
    z-index: -1;
  }
}
