.book__section-header {
  font-size: rem(20);
  font-weight: 600;
  line-height: 1.2;
  margin-bottom: rem(20);

  @include breakpoints(large) {
    font-size: rem(32);
    margin-bottom: rem(30);
  }
}

.book__section-container {
  margin: 0 auto;
  max-width: $site-max-width;
  padding: rem(40) 1rem;

  @include breakpoints(large) {
    padding: rem(100) 2rem;
  }
}
