.post-conversations__header-title {
	font-weight: 500;
	margin-bottom: rem(24);
	text-transform: uppercase;
}

.post-conversations__list {
	@include vertical-rhythym(rem(10));
}

.post-conversations__list-item {
	margin-top: 0;

	& ~ & {
		margin-top: rem(32);
	}
}

.post-conversations__item-link {
	color: $steel;
	text-decoration: none;
}

.post-conversations__item-title {
	line-height: 1.25;
	font-size: rem(18);

	@include breakpoints(medium) {
		font-size: rem(16);
	}
}
