// Content Single post header styles

/* stylelint-disable */

.entry-header__title {
	font-size: rem(32);
	font-weight: 500;
	line-height: 1.2em;
	margin-bottom: rem(20);
}

.byline--entry-header {
	letter-spacing: calc(-5 / 1000em);
}

.header-big2023 {
    h2 {
        color: #FFFFFF;
        text-align: center;
        font-weight: 500;
        font-size: 32px;
        line-height: 1.2;

        @include breakpoints(medium) {
            font-size: 50px;
        }

        @include breakpoints(large) {
            font-size: 60px;
        }
    }
}

$big2023_steps: 8;

.header-big2023 {
    padding: 60px 0;
    width: 100%;
    position: relative;

    @include breakpoints(large) {
        padding: 90px 0;
    }

    .container {
        position: relative;
    }

    h2 {
        color: #FFFFFF;
        text-align: center;
        font-weight: 500;
        font-size: 50px;
        line-height: 1.22;

        @include breakpoints(large) {
            font-size: 64px;
        }
    }

    strong {
        display: flex;
        flex-wrap: nowrap;

        span {
            width: 100%;
            flex: 0 0 100%;
            opacity: 0;

            &:nth-child(1) {
                opacity: 1;
            }

            @for $i from 1 through $big2023_steps {
                &:nth-child(#{$i}) {
                    transform: translateX(-#{ ($i - 1) * 100% });
                }
            }
        }
    }

    + #main {
        .blog-section {
            padding-top: rem(50);

            @include breakpoints(large) {
                padding-top: rem(60);
            }
        }
    }
}

.header-big2023--pattern {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;

    svg {
        width: 100%;
        height: 100%;
        display: block;

        path {
            stroke: var(--color-header);
        }
    }
}

/* stylelint-enable */
