.cta__banner {
  @include full-bleed;
  background-color: $color-navy;
  color: $white;
}

.cta__container {
  align-items: center;
  display: flex;
  margin: 0 auto;
  max-width: $site-max-width;
  padding: rem(25) 0;

  @include breakpoints(none, medium) {
    flex-wrap: wrap;
    padding-left: 1rem;
    padding-right: 1rem;
  }

  @include breakpoints(medium) {
    padding-left: 2rem;
    padding-right: 2rem;
  }
}

.siema {
  flex-basis: 100%;

  @include breakpoints(medium) {
    flex-basis: 90%;
  }
}

.cta__buttons {
  flex-basis: 10%;
  margin-left: 40px;

  @include breakpoints(none, medium) {
    display: flex;
    margin-left: auto;
  }

  .slide-control {
    background-color: transparent;
    border: 1px solid white;
    height: 21px;
    border-radius: 50%;
    width: 21px;

    // stylelint-disable-next-line selector-max-specificity
    &:not(:last-child) {
      margin-right: rem(5);
    }

    // stylelint-disable-next-line selector-max-specificity
    &:hover {
      cursor: pointer;
      border-color: $color-darktan;
    }

    // stylelint-disable-next-line selector-max-specificity
    &:focus {
      outline: none;
      border-color: $color-darktan;
    }
  }

  .active {
    background-color: $color-darktan;
  }
}

.cta__text {
  font-size: rem(18);

  a {
    color: $color-darktan;
    text-decoration: none;

    &:hover,
    &:focus {
      color: $color-darkdarktan;
    }
  }
}
