// Content Single layout styles

.post {
	padding-top: rem(40);
}

.entry-header {
	margin-bottom: rem(30);
}

.entry-content {
	display: flex;
	flex-direction: column;

	@include breakpoints(medium) {
		flex-direction: row;
		justify-content: space-between;
	}
}

.entry-content__primary {
	order: 1;

	.sidebar--left & {
		@include breakpoints(medium) {
			width: 75%;
		}
	}
}

.related-posts--entry-content {
	display: none;

	@include breakpoints(medium) {
		display: block;
	}
}

.related-posts--entry-footer {
	border-top: $border-medium;
	padding-top: rem(24);

	@include breakpoints(medium) {
		display: none;
	}
}

.sidebar--single {
	@include breakpoints(medium) {
		width: 20%;
	}

	.entry-footer & {
		padding-bottom: rem(32);

		@include breakpoints(medium) {
			display: none;
		}
	}
}

.sidebar__widgets {

	.entry-content & {
		margin-bottom: rem(30);
	}

	.widget--subscribe {
		display: none;

		@include breakpoints(large) {
			display: block;
		}
	}
}

.widget--post-terms {

	.entry-content & {
		@include breakpoints(medium) {
			margin-bottom: rem(20);
		}
	}

	.entry-footer & {
		display: none;
	}
}

.widget--post-social,
.widget--conversations {

	.entry-content & {
		display: none;

		@include breakpoints(medium) {
			display: block;
		}
	}

	.entry-footer & {
		@include breakpoints(medium) {
			display: none;
		}
	}
}

.widget--post-social {

	.entry-content & {
		@include breakpoints(medium) {
			margin-bottom: rem(80);
		}
	}

	.entry-footer & {
		margin-bottom: rem(0);

		@include breakpoints(medium) {
			margin-bottom: rem(60);
		}
	}
}

.widget--subscribe {

	/* stylelint-disable selector-max-specificity */
	.entry-content__primary & {
		margin-bottom: rem(80);
		margin-top: rem(40);

		@include breakpoints(medium) {
			margin-bottom: rem(50);
			margin-top: 0;
		}

		.subscribe__form {
			@include breakpoints(medium) {
				max-width: rem(300);
			}
		}
	}

  &.widget--subscribe-footer {
    padding-top: rem(50);
    border-top: $border-light;
  }

  &.widget--subscribe-sidebar {
    display: none;
  }

  &.widget--subscribe-inline {
    display: block;
  }
	/* stylelint-enable */
}

.table-rank {
	font-size: 12px;
	min-width: 800px;

	&-wrap {
		overflow-x: auto;
	}

	th,
	td {
		border: none;
	}

	td {
		border: solid #c9c9c6;
		border-width: 1px 0;
	}

	.rank {
		text-align: center;
	}

	.clogo {
		width: 110px;

		img {
			display: inline-block;
			margin-bottom: 0;
			transform: scale(0.89);
		}
	}

	.hq {
		min-width: 120px;
	}

	.website {
		span {
			@media only screen and (max-width: 1023px) {
				display: none;
						}
		}

		em {
			font-style: normal;

			@media only screen and (min-width: 1024px) {
				display: none;
			}
		}
	}
}
