/*
 * Blog page layout & content styles
 */

/* stylelint-disable selector-max-specificity */

.page-template-page_template-article,
.post-template-default.single.single-post,
.page-template-page_template-article-sidebar {
  .site {
    overflow-x: hidden;
  }

  .content-new {
    &,
    & > .container {
      max-width: none;
      padding: 0;
    }
  }

  #footer {
    background: #F2F2F2;
    margin-top: 0;
  }

  .container {
    margin-bottom: 0;
  }

  .site-content__primary {
    margin-bottom: 0;
  }

  .input-addon .mktoFormRow .mktoFieldWrap .mktoEmailField {
    color: $steel;

    &::placeholder {
      color: #a4a6a8;
      opacity: 1;
    }
  }

  .input-addon {
    display: flex;
    max-width: 375px;
    margin: 0 auto;
    height: 44px;
    // stylelint-disable-next-line declaration-no-important
    padding: 0 !important;

    .mktoFormRow {
      flex: 1;
      width: 100%;
      // stylelint-disable-next-line declaration-no-important
      display: none !important;

      &:first-of-type {
        // stylelint-disable-next-line declaration-no-important
        display: inline-block !important;
      }

      .mktoFieldDescriptor {
        // stylelint-disable-next-line declaration-no-important
        margin: 0 !important;
        height: 100%;
      }

      .mktoFieldWrap {
        height: 100%;

        .mktoEmailField {
          flex: 1;
          width: 100%;
          background: $white;
          padding: 6px 15px;
          border: 1px solid $white;
          outline: none;
          font-size: 16px;
          font-family: font-family(sans-serif);
          // stylelint-disable-next-line declaration-no-important
          height: 100% !important;
        }
      }
    }

    &.mktoForm {
      .mktoGutter,
      label {
        // stylelint-disable-next-line declaration-no-important
        display: none !important;
      }
    }

    .mktoButtonRow {
      width: 95px;
      height: 44px;

      .mktoButtonWrap {
        button.mktoButton {
          width: 95px;
          height: 44px;
          // stylelint-disable-next-line declaration-no-important
          background: $poppy !important;
          // stylelint-disable-next-line declaration-no-important
          border: 1px $poppy !important;
          outline: none;
          padding: 0;
          display: flex;
          justify-content: center;
          align-items: center;
          color: $white;
          // stylelint-disable-next-line declaration-no-important
          font: 0.8rem / 1 font-family(sans-serif) !important;

          &:hover {
            // stylelint-disable-next-line declaration-no-important
            background: $poppy !important;
            // stylelint-disable-next-line declaration-no-important
            border: 1px $poppy !important;
          }

          &::after {
            content: 'Sign Up';
            // stylelint-disable-next-line declaration-no-important
            font: 1rem / 1 font-family(sans-serif) !important;
          }

          &[disabled] {
            &::after {
              content: none;
            }
          }

          > img {
            display: none;
          }

          .subscribe-input__btn {
            display: none;
            height: 20px;
            max-width: 16px;
            width: 16px;
          }
        }
      }
    }

    input:not([type="submit"]) {
      flex: 1;
      width: 100%;
      background: $white;
      padding: 6px 15px;
      border: 1px solid $white;
      outline: none;
      font-size: 16px;
    }

    button {
      width: 44px;
      height: 44px;
      background: $poppy;
      border: none;
      outline: none;
      padding: 0;
      font-size: 24px;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $white;
    }
  }
}

.post-template-default.single.single-post.enable-footer-cta {

  #footer {
    background: $black;
    color: $white;
    margin-top: 0;
  }

  .footer-new {

    .copyright {
      color: rgba($white, 0.9);
    }

    .social-networks {

      li {

        a {
          transition: background 0.3s linear;
          background: $white;

          &:hover {
            background: $future_blue;
          }
        }
      }
    }

    .footer-nav {

      ul {

        li {

          + li {

            &::before {
              background: $white;
            }
          }

          a {
            transition: color 0.3s linear;
            color: $white;

            &:hover {
              color: $future_blue;
            }
          }
        }
      }
    }
  }
}

