.blog-aside {
  margin-bottom: rem(30);

  @include breakpoints(s-medium) {
    width: 220px;
    margin-bottom: 0;
  }

  @include breakpoints(s-large) {
    width: 250px;
  }

  @include breakpoints(m-large) {
    width: 282px;
  }
}

.blog-aside__inner,
.blog-aside-right__inner {
  height: 100%;

  @include breakpoints(s-medium) {
    padding-right: rem(30);
  }

  @include breakpoints(m-large) {
    padding-right: rem(60);
  }

  // stylelint-disable
  @media only screen and (max-width: 767px) {
    position: static !important;
    transform: none !important;
    width: 100% !important;
  }

  .post-terms {
    margin-bottom: rem(20);

    @include breakpoints(s-medium) {
      margin-bottom: rem(40);
    }
  }

  .post-terms a {
    transition: color 0.3s linear;
  }
}

.blog-social-networks {
  display: flex;
  flex-wrap: wrap;
  margin: rem(0 -16 25);

  @include breakpoints(s-medium) {
    margin-bottom: rem(45);
  }

  li {
    padding: rem(0 16);
  }

  a {
    text-decoration: none;
    color: #98999b;
    font-size: 24px;
    transition: color 0.3s linear;

    &:hover {
      color: $poppy;
    }
  }
}

.toc-mobile-wrapper {
  border-left: 2px solid $poppy;
  padding: rem(8 0 5 22);
  margin: 0 0 rem(30) 0;

  @include breakpoints(s-medium) {
    display: none !important;
  }

  h4 {
    display: block;
    padding-bottom: rem(17);
    border-bottom: 1px solid #ededed;
    margin-bottom: rem(15);
  }
}

.toc-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.toc {
  border-left: 2px solid $poppy;
  padding: rem(8 0 5 22);
  display: none;

  @include breakpoints(s-medium) {
    padding: rem(15 0 5 24);
    display: block;
  }

  .title {
    display: block;
    padding-bottom: rem(17);
    border-bottom: 1px solid #ededed;
    margin-bottom: rem(15);
  }

  .sidebar_menu {
    .title {
      padding-bottom: rem(10);
      margin-bottom: rem(10);
    }

    .side-sub-menu {
      margin-bottom: rem(20);
    }

    .toc-list {
      li {
        margin: 0 0 rem(3) 0;

        &.current_page_item {
          a {
            color: $poppy;

            &::before {
              opacity: 1;
              visibility: visible;
            }
          }
        }
      }
    }
  }
}

.toc-list {

  li {
    list-style: none;
    margin: 0 0 rem(11) 0;

    &:last-child {
      margin-bottom: 0;
    }
  }

  a {
    text-decoration: none;
    color: $concrete;
    transition: color 0.3s linear;
    position: relative;
    font-weight: 300;

    &:focus {
      color: $concrete;
      outline: none;

      &::before {
        opacity: 0;
        visibility: hidden;
      }
    }

    &:hover {
      color: $poppy;

      &::before {
        opacity: 1;
        visibility: visible;
      }
    }

    &.active {

      @include breakpoints(s-medium) {
        color: $poppy;

        &::before {
          opacity: 1;
          visibility: visible;
        }
      }
    }

    &::before {
      content: "";
      opacity: 0;
      visibility: hidden;
      transition: opacity 0.3s linear, visibility 0.3s linear;
      width: 0;
      height: 0;
      border-style: solid;
      border-width: 4px 0 4px 6px;
      border-color: transparent transparent transparent $poppy;
      position: absolute;
      left: -24px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}

.blog-content {
  flex: 1;
  line-height: 1.63;

  @include breakpoints(s-medium) {
    width: 220px;
    max-width: calc(100% - 220px);
  }

  @include breakpoints(s-large) {
    max-width: calc(100% - 250px);
  }

  @include breakpoints(m-large) {
    max-width: calc(100% - 282px);
  }

  h1 {
    &.heading {
      font-size: rem(23);
      line-height: 1.6;
      color: $poppy;
      margin-bottom: rem(26);
    }
  }

  h2 {
    &.heading {
      font-size: rem(21);
      line-height: 1.6;
      color: $steel;
      margin-bottom: rem(24);
    }
  }

  h3 {
    &.heading {
      font-size: rem(19);
      line-height: 1.6;
      color: $bayside;
      margin-bottom: rem(22);
    }
  }

  h4 {
    &.heading {
      font-size: rem(17);
      line-height: 1.6;
      color: $concrete;
      margin-bottom: rem(19);
    }
  }

  h5 {
    &.heading {
      font-size: rem(16);
      line-height: 1.6;
      color: $poppy;
      margin-bottom: rem(19);
      text-transform: uppercase;
    }
  }

  h1, h2, h3, h4, h5, h6 {
    &.heading {
      &.color-orange {
        color: $poppy;
      }
      &.color-dark-gray {
        color: $steel;
      }
      &.color-light-blue {
        color: $bayside;
      }
      &.color-light-gray {
        color: $concrete;
      }
      &.color-bio {
        color: $bayside;
      }
      &.color-enterprise {
        color: $deep_ocean;
      }
      &.color-consumer {
        color: $avocado;
      }
      &.color-crypto {
        color: $grapevine;
      }
      &.color-fintech {
        color: $marina;
      }
    }
  }

  h2 {
    font-size: rem(21);
    line-height: 1.6;
    color: $poppy;
    margin-bottom: rem(20);

    @include breakpoints(s-medium) {
      margin-bottom: rem(32);
    }
  }

  h2.style-1 {
    font-size: rem(32);
    color: $steel;
    font-weight: 400;
  }

  p {
    margin-bottom: rem(26);
  }

  .box {
    border: 1px solid $fog;
    padding: rem(20);

    @include breakpoints(s-large) {
      padding: rem(39 38);
    }

    h4 {
      font-size: rem(24);
      line-height: 1.2;
      font-weight: 400;
      margin-bottom: rem(20);

      @include breakpoints(s-medium) {
        margin-bottom: rem(40);
      }
    }
  }
}

