  font-size: 16px;
  line-height: 1.2;
  font-weight: 600;
  transition: all 0.3s linear;
  color: $white;
  text-decoration: none;
  text-align: center;

  @include breakpoints(s-medium) {
    padding: 11px 21px;
  }

  &.btn-orange {
    transition: background 0.3s linear;
    background: $poppy;
    color: $white;
    text-decoration: none;

    &:hover {
      background: $poppy;
      color: $white;
    }
  }
}

.related-posts-new {
