.card-border {
  padding: 1.5rem;
  margin-bottom: 1rem;
  background-color: #fff;
  border-radius: .25rem;
  border: 1px solid rgba(0, 0, 0, 0.125);
}

.page-template-page_template-article-sidebar {
  @include breakpoints(none, s-medium) {
    #primary {
      position: relative;
      padding-top: 60px;
    }

    .blog-aside {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      margin-bottom: 0;
      pointer-events: none;
    }

    &.sticky-toc {
      .blog-aside {
        position: fixed;
        z-index: 2;
      }
    }

    .sidebar_menu {
      background: $white;
      height: 100vh;
      padding: 30px 15px 0;
      overflow-y: auto;
      opacity: 0;
      visibility: hidden;
      transition: opacity .3s ease-in-out,visibility .3s ease-in-out;
    }

    &.toc-active {
      //overflow: hidden;

      .sidebar_menu {
        opacity: 1;
        visibility: visible;
      }

      #toc-opener {
        &:after {
          transform: rotate(180deg);
        }
      }

      .blog-aside {
        pointer-events: all;
      }
    }

    .toc {
      display: block;
      border: 0 none;
      padding: 0;
    }

    #toc-opener {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding: 11px 15px;
      background-color: #ed8b00;
      color: #fff;
      position: relative;
      height: 46px;
      pointer-events: all;

      &:after {
        content: "\E902";
        font: 10px/1 icomoon;
      }
    }
  }

  #toc-opener {
    @include breakpoints(s-medium) {
      display: none;
    }
  }

  .article-pagination {
    margin-top: 50px;
    display: flex;
    padding: 15px 0;
    border-top: 1px solid #ededed;
    border-bottom: 1px solid #ededed;

    a {
      flex: 1 0 50%;
      color: $concrete;
      text-decoration: none;
      display: flex;
      align-items: center;

      &:hover {
        color: $poppy;
      }
    }

    .entry-content-next {
      position: relative;
      text-align: right;
      justify-content: flex-end;

      .arrows {
        padding-right: 20px;

        i {
          position: absolute;
          top: 50%;
          right: 0;
          transform: translateY(-50%);
        }
      }
    }

    .entry-content-prev {
      position: relative;
      text-align: right;

      .arrows {
        padding-left: 20px;

        i {
          position: absolute;
          top: 50%;
          left: 0;
          transform: translateY(-50%);
        }
      }
    }
  }
  .toc-wrapper {
    @include breakpoints('none', s-medium) {
      z-index: 2;
    }
  }
}

