.article-banner {
    &,
    .wp-content & {
        overflow: hidden;
        background-color: #f6f6f6;
        color: $white;
        background-repeat: no-repeat;
        background-position: 100% 0;
        background-size: cover;

        @media only screen and (min-width: 768px) {
            display: flex;
            flex-direction: row-reverse;
            justify-content: space-between;
        }

        .content {
            padding: 37px 32px 45px;
            background: #4100ff;
            max-width: 250px;
            flex: 0 0 250px;
            margin: 0 90px 0 0;
            position: relative;
            z-index: 1;

            @media only screen and (max-width: 1023px) {
                max-width: 250px;
                flex: 0 0 250px;
                padding: 30px 15px;
            }

            @media only screen and (max-width: 767px) {
                max-width: none;
                margin-right: 0;
            }

            &:after {
                position: absolute;
                bottom: 149px;
                right: -305px;
                content: "";
                background: #4100ff;
                width: 800px;
                height: 300px;
                z-index: -1;
                transform: rotate(55deg) skew(6deg);

                @media only screen and (max-width: 1023px) {
                    height: 200px;
                    transform: rotate(75deg) skew(3deg);
                    bottom: 290px;
                    right: -320px;
                }

                @media only screen and (max-width: 767px) {
                    display: none;
                }
            }
        }

        .text-wrap {
            margin: 0 -180px 0 0;

            @media only screen and (max-width: 1023px) {
                margin: 0 -95px 0 0;
            }

            @media only screen and (max-width: 767px) {
                margin: 0;
            }
        }

        h4 {
            font-size: rem(28);
            line-height: 1.2143;
            margin-bottom: 6px;
            color: $white;
            text-transform: none;

            @media only screen and (max-width: 1023px) {
                font-size: rem(24);
            }
        }

        p {
            margin-bottom: 24px;
        }

        a {
            color: #4100ff;
            text-decoration: none;
        }

        .logo-hold {
            padding: 31px 36px;
            max-width: 265px;
            flex: 0 0 265px;
            margin: 0;

            @media only screen and (max-width: 1023px) {
                padding: 30px 15px;
                max-width: 165px;
                flex:  0 0 165px;
            }
        }
    }
}

.ssb-post-header {
  position: relative;
  z-index: 1;

  .container > .link-back-wrapper {
    display: none;
  }

  @include breakpoints(none, s-medium) {
    margin-bottom: 50px;

    .container > .link-back-wrapper {
      display: block;
      text-align: center;
      padding: 10px 30px 0;
    }
  }
}

.ssb-post-header--inner .link-back-wrapper,
.ssb-post-header .link-back-wrapper {
  position: relative;
  z-index: 1;

  a {
    position: relative;
    font-size: 18px;
    line-height: 40px;
    color: $black;
    font-weight: 900;
    text-decoration: none;
    display: inline-flex;
    align-items: center;

    @include breakpoints(none, s-medium) {
      font-size: 16px;
    }

    &::after {
      content: '\e91a';
      font: 20px/1 "icomoon";
      margin-left: 12px;
      color: $poppy;
    }
  }
}

.ssb-post-header--inner {
  height: 380px;
  margin-left: -25px;
  margin-right: -25px;
  display: flex;

  @include breakpoints(none, s-medium) {
    background-color: $poppy;
  }

  .image-center {
    position: absolute;
    width: 100%;
    top: 50%;
    left: 50%;
    max-width: 80%;
    transform: translate(-50%, -50%);

    @include breakpoints(none, s-medium) {
      max-width: 240px;
      top: 36%;
    }

    img {
      display: block;
      width: 100%;
    }
  }

  @include breakpoints(none, s-medium) {
    .link-back-wrapper {
      display: none;
    }
  }
}

.ssb-post-header--right {
  background-color: $poppy;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: calc(50% + 300px);
  z-index: -1;
  background-size: cover;
  background-position: 100% 50%;

  @include breakpoints(none, s-medium) {
    display: none;
  }
}

.ssb-post-header--left {
  height: 100%;
  padding: 25px 10px 25px 25px;
  display: flex;
  position: relative;
  z-index: 1;
  align-items: flex-end;
  justify-content: center;

  @include breakpoints(s-medium) {
    min-width: 390px;
  }

  &::after,
  &::before {
    content: "";
    position: absolute;
    height: 100%;
    top: 0;

    @include breakpoints(none, s-medium) {
      display: none;
    }
  }

  &::before {
    width: 183px;
    background: url('/wp-content/themes/a16z-2015/client/images/post-header-right.svg') no-repeat 100% 50% / cover;
    left: 100%;
    z-index: 0;
    margin-left: -60px;
  }

  &::after {
    left: -9999px;
    right: 0;
    z-index: -1;
    background-color: #efefef;
  }

  @include breakpoints(none, s-medium) {
    width: 100%;
    background-color: transparent;
  }
}

.m100-post-header {
  position: relative;
  z-index: 1;
  background-color: $white;
  background-size: cover;
  background-position: 50% 50%;

  @include breakpoints(none, s-medium) {
    margin-bottom: 50px;

    .container > .link-back-wrapper {
      display: block;
      text-align: center;
      padding: 10px 30px 0;
    }
  }

  .container {
    max-width: calc(1160px + 2%);
    padding-left: 2%;
    padding-right: 2%;
  }
}

.m100-post-header--inner {
  height: 380px;
  padding-top: 120px;
  margin-left: -25px;
  margin-right: -25px;
  display: flex;

  a.header-img-wrapper {
    display: inline-block;
  }

  .image-center {
    width: 100%;
    text-align: center;
  }

  img {
    display: inline-block;
  }
}

.m100-post-header--left {
  height: 100%;
  padding: rem(25 25 45 25);
  display: flex;
  position: relative;
  z-index: 1;
  width: 100%;
  align-items: center;
}

.ssb-post-type,
.m100-post-type {
  .header-new {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: none;
  }

  .content-new > .container,
  .footer-new { margin: 0 auto; }

  &.ssb-post-header-logo-color-white {
    &:not(.nav-active) {
      .nav-opener span,
      .nav-opener::before,
      .nav-opener::after {
        background: $white;
      }

      .logo-wrap svg path {
        fill: $white;
      }

      .header-new .tagline {
        color: $white;
      }
    }
  }

  &.ssb-post-header-color-white {
    @include breakpoints(none, s-medium) {
      &:not(.nav-active) {
        .nav-opener span,
        .nav-opener::before,
        .nav-opener::after {
          background: $white;
        }

        .logo-wrap svg path {
          fill: $white;
        }

        .header-new .tagline {
          color: $white;
        }
      }
    }

    &:not(.nav-active) {
      .header-new .header-holder .search .icon-search {
        color: $white;
      }
    }

    @include breakpoints(s-medium) {
      #nav > ul > li > a {
        color: $white;
      }
    }
  }
}

.ssb-post-type {
  @include breakpoints(none, s-medium) {
    .nav-opener span,
    .nav-opener::before,
    .nav-opener::after {
      background: #011209;
    }

    .logo-wrap svg path {
      fill: #011209;
    }

    .header-new .tagline {
      color: #011209;
    }
  }

  @include breakpoints(s-medium) {
    .nav-opener span,
    .nav-opener::before,
    .nav-opener::after {
      background: #011209;
    }

    .logo-wrap svg path {
      fill: #011209;
    }

    .header-new .tagline {
      color: #011209;
    }

    #nav > ul > li > a {
      color: #011209;
    }
  }

  .header-new .header-holder .search .icon-search {
    color: #011209;
  }
}

