/* stylelint-disable selector-max-specificity */

.wp-content {
  .share-image-tweet-wrapper {
    &.share-image-align-left {
      @include breakpoints(medium) {
        margin: 0 auto 0 0;
        max-width: 560px;
      }
    }

    &.share-image-align-center {
      @include breakpoints(medium) {
        margin: 0 auto;
        max-width: 560px;
      }
    }


    img {
      display: block;
      width: 100%;
    }
  }

  .share-image-tweet {
    @include breakpoints(medium) {
      display: flex;
    }

    a {
      display: inline-block;
      font-weight: 800;
      font-size: 14px;
      line-height: 17px;
      text-transform: uppercase;
      color: #5BC2E7;
      text-decoration: none;

      &:after {
        font: 16px/1 icomoon;
        content: "\e907";
        color: #5BC2E7;
        margin-left: 10px;
      }
    }

    .share-image-caption {
      font-weight: 500;
      font-size: 12px;
      line-height: 16px;
      color: #97999B;
      margin-bottom: rem(12);

      @include breakpoints(medium) {
        order: 2;
        padding-left: 40px;
        text-align: right;
        max-width: 370px;
        margin-left: auto;
      }
    }
  }
}

.wp-content ul {
  &.bio-lp-subscribe__apps {
    @extend %listreset;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
    margin: -10px -15px 10px;
    max-width: 1070px;
    width: 100%;

    @include breakpoints(none, s-medium) {
      margin: 0 -5px;
    }

    li {
      display: flex;
      margin: 0 0 20px;
      padding: 0 15px;
      max-width: 25%;
      width: 25%;
      flex: 1 0 25%;

      @include breakpoints(none, s-large) {
        width: 33.333%;
        max-width: 33.333%;
        margin: 0;
        padding: 0 5px;
        flex: 1 0 33.333%;
        margin-bottom: 15px;
      }

      a {
        position: relative;
        width: 100%;
        height: 0;
        border-radius: 8px;
        display: block;
        overflow: hidden;
        padding: 0 0 24.35% 0;

        img {
          display: block;
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
        }

        span {
          display: flex;
          align-items: center;
          justify-content: center;
          position: absolute;
          width: 100%;
          height: 100%;
          left: 0;
          top: 0;
        }
      }
    }
  }
}
/* stylelint-enable selector-max-specificity */
