.book-media {
    margin-left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    font: 300 24px/1.23 $din-condensed;
    text-transform: uppercase;

    @include breakpoints(large) {
        font-size: 31px;
    }
    
    .book__section-container {
        @include breakpoints(medium) {
            display: flex;
            align-items: center;
        }
    }
    
    .img {
        margin-bottom: 30px;

        @include breakpoints(medium) {
            width: 30%;
            margin: 0 30px 0 0;
        }
    }
    
    .desc {
        flex: 1;
    }

    p {
        margin-bottom: 25px;

        strong {
            font-weight: 300;
            color: $bayside;
        }
    }
}
